// EditUI dialog
.c-multi-item-dialog .mat-dialog-container {
  padding: 0;
  background-color: $edit-ui-dialog-background-color;
  border-radius: $edit-ui-dialog-border-radius;

  @media (max-width: 600px) {
    border-radius: 0;
  }

  bottom: 10px;
}

.c-multi-item-dialog {
  width: 100vw;
  max-width: 800px !important;
  margin-top: $edit-ui-dialog-top-padding !important;

  @media (max-width: 600px) {
    margin-top: 0 !important;
    height: 100vh;
  }
}

app-collapsible-wrapper {
  .description p:last-child {
    margin-bottom: 0;
  }

  app-collapsible-wrapper:last-child {
    mat-card:last-child {
      margin: 0;
    }
  }
}
